import React from 'react';
import styled, { css } from 'styled-components';

import { ColorLuminance } from '../lib';

const Container = styled.ul`
  list-style: none;
  margin: 0 2em;
  padding-left: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  @media (max-width: 500px) {
    justify-content: center;
  }
`;

const ProjectContainer = styled.li`
  padding: 0.5em;
  box-sizing: border-box;
  width: 33%;

  @media (min-width: 1290px) {
    width: 25%;
  }

  @media (max-width: 800px) {
    width: 50%;
  }

  @media (max-width: 500px) {
    width: 100%;
  }
`;

const Project = styled.div`
  overflow: hidden;
  box-shadow: 0 15px 35px rgba(50, 50, 93, 0.1), 0 5px 15px rgba(0, 0, 0, 0.07);
  border-radius: 4px;
  height: 11.5em;
  display: flex;
  flex-direction: column;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.125);
`;

const Tags = styled.div`
  color: #fff;
  flex-grow: 1;
  display: flex;
  align-items: flex-end;
`;

const Tag = styled.span`
  font-size: 17px;
  padding: 0.3em 0.5em;
  border-radius: 4px;
  margin: 0 0.3em;

  @media (max-width: 800px) {
    font-size: 14px;
  }
`;

const ProjectInfo = styled.div`
  padding: 0.7em;
  background: ${props => props.color};
  flex-grow: 1;
  display: flex;
  flex-direction: column;

  & ${Tag} {
    background: ${props => ColorLuminance(props.color, -0.15)};
  }
`;

const Title = styled.h3`
  margin: 0;
  color: ${props => props.color};
`;

const Text = styled.p`
  color: #fff;
  font-size: 0.8em;
`;

const ActionLink = styled.a`
  text-decoration: none;
  width: 50%;
  color: #fff;

  ${props =>
    props.disabled &&
    css`
      pointer-events: none;
      opacity: 0.2;
    `};
`;

const Actions = styled.div`
  width: 100%;
  display: flex;
  line-height: 2.5em;
  text-align: center;
  text-transform: uppercase;
  font-size: 0.8em;
  background: ${props => ColorLuminance(props.color, -0.15)};

  ${ActionLink}:hover {
    background: ${props => ColorLuminance(props.color, -0.5)};
  }
`;

const Filler = styled.li`
  height: 0;
  width: 33%;

  @media (min-width: 1290px) {
    width: 25%;
  }

  @media (max-width: 800px) {
    width: 50%;
  }

  @media (max-width: 500px) {
    width: 0;
  }
`;

const renderProjects = () => {
  const projects = [
    {
      name: 'Rideaway',
      description:
        'A routeplanner aiming to improve the experience of bikers in Brussels. ',
      colorPrimary: '#fbba00',
      colorSecondary: '#fff',
      tags: ['ES6', 'PWA'],
      link: 'http://routeplanner.bike.brussels',
      git: 'https://github.com/oSoc17/rideaway-frontend'
    },
    {
      name: 'Vouddit',
      description: 'A video browser for reddit built using React-Redux. ',
      colorPrimary: '#151516',
      colorSecondary: '#fff',
      tags: ['React', 'Redux', 'TypeScript'],
      link: 'https://vouddit.netlify.com',
      git: 'https://github.com/ArnaudWeyts/Vouddit'
    },
    {
      name: 'iRail Datawall',
      description: 'The iRail data wall at De Krook library in Ghent. ',
      colorPrimary: '#c91f1b',
      colorSecondary: '#fff',
      tags: ['React'],
      link: 'https://datawall.herokuapp.com',
      git: 'https://github.com/iRail/datawall'
    },
    {
      name: 'Monumentrun',
      description:
        'Hackathon creation to discover monuments whilst keeping in shape. ',
      colorPrimary: '#C95442',
      colorSecondary: '#fff',
      tags: ['React'],
      link: 'https://monumentrunweb.netlify.com',
      git: 'https://github.com/Haroenv/monumentrun-web'
    },
    {
      name: 'Ella Website',
      description: 'Frontend website for a smart assistant bot. ',
      colorPrimary: '#0D8EC5',
      colorSecondary: '#fff',
      tags: ['React'],
      link: 'https://ella.netlify.com',
      git: 'https://github.com/B-AIM-KnowledgeFlow/website'
    },
    {
      name: 'Tryppio',
      description:
        'Tryppio is a citytrip-planning app that plans your holiday based on your preferences.',
      colorPrimary: '#40a9ff',
      colorSecondary: '#fff',
      tags: ['React', 'Redux', 'PWA'],
      link: 'https://tryppio.netlify.com',
      git: 'https://github.com/ArnaudWeyts/tryppio'
    },
    {
      name: 'Ella Chatbot',
      description:
        'Ella is a smart assistant chatbot created during an international project.',
      colorPrimary: '#0D8EC5',
      colorSecondary: '#fff',
      tags: ['Node.JS'],
      link: 'https://ella.netlify.com',
      git: null
    },
    {
      name: 'Punchtime',
      description: 'Application to keep track of employee hours.',
      colorPrimary: '#3B4358',
      colorSecondary: '#fff',
      tags: ['Android', 'Java'],
      link: null,
      git: 'https://github.com/punchtime/android'
    }
  ];

  return projects.map(p => {
    return (
      <ProjectContainer key={p.name}>
        <Project>
          <ProjectInfo color={p.colorPrimary}>
            <Title color={p.colorSecondary}>{p.name}</Title>
            <Text>{p.description}</Text>
            <Tags>{p.tags.map(t => <Tag key={t}>{t}</Tag>)}</Tags>
          </ProjectInfo>
          <Actions color={p.colorPrimary}>
            <ActionLink disabled={!p.link} href={p.link}>
              View
            </ActionLink>
            <ActionLink disabled={!p.git} href={p.git}>
              GitHub
            </ActionLink>
          </Actions>
        </Project>
      </ProjectContainer>
    );
  });
};

const Projects = () => {
  return (
    <Container>
      {renderProjects()}
      <Filler />
      <Filler />
    </Container>
  );
};

export default Projects;



