import React, { Component } from 'react';
import styled from 'styled-components';

import NameSynonym from './NameSynonym';
// import Bubble from './Bubble';
import Projects from './Projects';

const Container = styled.div`
  height: 100vh;
  width: 100vw;
  overflow-x: hidden;
  background: #202124;
  padding-bottom: 2em;
  box-sizing: border-box;
`;

const Header = styled.header`
  height: 5em;
  display: flex;
  align-items: center;
  position: relative;
`;

class App extends Component {
  render() {
    return (
      <Container>
        <Header>
          <NameSynonym />
        </Header>
        <Projects />
      </Container>
    );
  }
}

export default App;



