import React, { Component } from 'react';
import styled, { css } from 'styled-components';

const Intro = styled.div`
  display: flex;
  height: 100%;
  width: 100%;
  align-items: center;
`;

const Title = styled.div`
  font-size: 2em;
  text-align: right;
  color: #fff;
  z-index: 1;
  flex-basis: 50%;
`;

const TitleFollowUp = Title.extend`
  position: absolute;
  left: 50%;
  color: ${props => props.color};
  opacity: 0;
  overflow: hidden;
  transition: position 0s linear 1s, opacity 1s linear;
  text-align: left;
  z-index: 1;

  ${props =>
    props.selected &&
    css`
      opacity: 1;
      position: static;
    `};
`;

const words = [
  { name: 'Developer', color: '#40a9ff' },
  { name: 'Photographer', color: '#26C6DA' },
  { name: 'Learner', color: '#1DE9B6' }
];

class NameSynonym extends Component {
  state = {
    wordIndex: 0
  };

  componentDidMount() {
    setInterval(() => {
      let nextIndex = this.state.wordIndex + 1;
      if (nextIndex > words.length - 1) {
        nextIndex = 0;
      }
      this.setState({
        wordIndex: nextIndex
      });
    }, 2500);
  }

  render() {
    return (
      <Intro>
        <Title>Arnaud</Title>
        {words.map(word => {
          return (
            <TitleFollowUp
              key={word.name}
              color={word.color}
              selected={
                words.indexOf(word) === this.state.wordIndex ? true : false
              }
            >
              {word.name}
            </TitleFollowUp>
          );
        })}
      </Intro>
    );
  }
}

export default NameSynonym;



